<template>
  <div id="job-table">
    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
      <!--列表-->
      <el-table @row-click="selectItem" :data="items" highlight-current-row style="width: 100%; margin-bottom:2px;">
        <el-table-column width="10"></el-table-column>
        <el-table-column type="index" width="40"></el-table-column>
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="[简介]">
                <div v-html="props.row.companyIntroduction" style="background:#e1e5f0;"></div>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="positionName" label="职位" width="250" sortable></el-table-column>
        <el-table-column prop="companyName" label="公司" width="250" sortable></el-table-column>
        <el-table-column prop="degreeRequired" label="职位要求" width="150" sortable></el-table-column>
        <el-table-column prop="salary" label="薪资" width="150" sortable></el-table-column>
        <el-table-column prop="areas" label="公司地点" width="150" sortable></el-table-column>
        <el-table-column prop="createTime" label="日期" width="150" sortable></el-table-column>
      </el-table>
      <el-col :span="24" class="toolbar">
        <el-pagination style="float:right;margin:10px" background layout="prev, pager, next" @current-change="handleCurrentChange" :total="parseInt(total)" :page-size="pageSize" :current-page.sync="currentPage">
        </el-pagination>
      </el-col>
    </el-col>
  </div>
</template>

<script>
import eventBus from "@/common/eventBus";
import jobOption from "@/common/jobSearch";
import Qs from "qs";

export default {
  name: "job-table",
  data() {
    return {
      items: [],
      total: 0, // 总条目数
      pageSize: 15, // 每页显示的数量
      currentPage: 1, //当前页
      loading: false,
      queryData: {
        content: jobOption.filters.name.trim(),
        regionIds: jobOption.regionIds,
        industryId: jobOption.industryId
      } //搜索框同步过来的数据
    };
  },
  methods: {
    //点击列表，进入详情页
    selectItem: function(row) {
      let data = JSON.stringify({ id: row.id });
      let path = "/recruitment_1";
      this.$router.push({ path: path, query: { queryMsg: data } });
    },
    //获取列表
    search() {
      let that = this;
      let url = that.HOST + "/position/search";
      let datas = that.queryData;
      datas.pageNum = that.currentPage;
      let params = Qs.stringify(datas);
      that.loading = true;
      that
        .axios({
          method: "post",
          data: params,
          url: url
        })
        .then(response => {
          that.loading = false;
          let message = response.data.message;
          that.total = message.total;
          that.pageSize = message.pageSize;
          that.items = message.list;
          message.list.forEach((item, index) => {
            that.items[index].createTime = item.createTime.slice(0, 10);
          });
        })
        .catch(response => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取招聘信息出现异常",
            duration: 2000
          });
        });
    },
    //换页
    handleCurrentChange(val) {
      this.currentPage = val;
      this.search(val, this.queryData);
    }
  },
  mounted() {
    let that = this;
    //接收job-search共享的全局数据并发送请求获取列表
    eventBus.$on("jobSearch", function() {
      that.queryData = {
        content: jobOption.filters.name.trim(),
        regionIds: jobOption.regionIds,
        industryId: jobOption.industryId
      };
      that.search();
    });
  },
  activated() {
    this.search();
  }
};
</script>

<style lang="scss" scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
@mixin whole {
  width: 100%;
  height: 100%;
}
#job-table {
  @include whole;
}
.active {
  color: #ed1c22 !important;
  border-bottom: 2px solid #ed1c22;
  padding-bottom: 4px;
}
.title {
  & {
    background: #7b7b7b;
    width: 100%;
  }
  li {
    & {
      display: inline-block;
    }
    a {
      color: #fff;
      cursor: default;
      font-size: 1em;
      font-weight: 500;
      line-height: 25px;
      vertical-align: middle;
      text-align: center;
      width: 100%;
      display: inline-block;
      box-sizing: border-box;
    }
  }
}
.content {
  & {
    width: 100%;
    margin: 5px 0;
  }
  &:focus a,
  &:hover a {
    white-space: initial;
    font-weight: 600;
  }
  li {
    & {
      display: inline-block;
    }
    a {
      font-size: 1em;
      color: #555555;
      font-weight: 500;
      line-height: 25px;
      vertical-align: middle;
      text-align: center;
      width: 100%;
      display: inline-block;
      box-sizing: border-box;
      max-height: 130px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: initial;
      // white-space: nowrap;
    }
  }
}
</style>



