<!--
 * @Description: 人才主页
 -->
 <template>
  <div id="recruitment-default">
    <div class="box1">
      <currentLocate></currentLocate>
      <!-- 职位搜索 -->
      <div class="box2">
        <ul class="title">
          <li class="list_title">
            <a class="active" style="margin-left:15px;">职位搜索</a>
          </li>
        </ul>
        <div style="width:100%;">
          <keep-alive>
            <job-search></job-search>
          </keep-alive>
        </div>
      </div>
      <!-- 职位列表 -->
      <div class="box1">
        <keep-alive>
          <job-table></job-table>
        </keep-alive>
      </div>
    </div>
  </div>
</template>
 <script>
import currentLocate from "@/components/common/tools/currentLocate";
import JobSearch from "@/components/common/tools/job-search";
import JobTable from "@/components/common/tools/job-table";

export default {
  name: "recruitment-default",
  data() {
    return {
      queryData: {}
    };
  },
  computed: {},
  methods: {
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "招聘"
      );
      global.currentPath.data = [index, 0];
    }
  },
  activated() {
    this.init();
  },
  components: {
    currentLocate,
    JobSearch,
    JobTable
  }
};
</script>
<style lang="scss" scoped>
$blue: #127de7;
#recruitment-default /deep/ img {
  max-width: 650px;
}
@mixin adv {
  margin: 5px auto;
  width: 1200px;
  height: 125px;
  overflow: hidden;
}
@mixin i-box {
  box-shadow: 0 0 1px #bebebe;
  height: 100%;
  overflow: hidden;
  border-radius: 2px;
}
@mixin border {
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: 1px solid rgba(204, 204, 204, 0.377);
}
#recruitment-default {
  @include adv;
  height: 100%;
}

/*小部件样式*/
.box1 {
  margin-top: 10px;
}
.box3 {
  @include i-box;
  min-height: 262px;
  margin-top: 12px;
  background: rgba(255, 255, 255, 0.95);
  -webkit-box-shadow: 0 0 1px #bebebe;
  box-shadow: 0 0 1px #bebebe;
}
.box2 {
  & {
    @include i-box;
    @include border;
    background: rgba(255, 255, 255, 0.95);
    margin-top: 10px;
    padding: 10px 20px;
  }
  .title {
    margin-bottom: 12px;
    margin-top: 2px;
    width: 100%;
    height: 28px;
    line-height: 28px;
    background: none;
  }
  .title li {
    float: left;
  }

  .list_title a {
    cursor: pointer;
    color: #7b7b7b;
    font-size: 1em;
    font-weight: 500;
    line-height: 25px;
    vertical-align: middle;
    margin-right: 10px;
    color: $blue;
    border-bottom: 2px solid $blue;
    padding-bottom: 4px;
  }
}
</style>



