<template>
  <div id="job-search">
    <div class="menus">
      <!-- 地区 -->
      <div class="row row-first">
        <div class="head">
          <span class="title" title='地区'>地区：</span>
        </div>
        <div class="body">
          <!-- 省份 -->
          <el-select v-model="value1" filterable placeholder="选择省份/直辖市/自治区" @change="changeArea">
            <el-option v-for="(item,index) in options1" :key="index" :label="item.name" :value="item.id">
              <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
            </el-option>
          </el-select>
          <!-- 城市 -->
          <el-select v-model="value2" :disabled="isBan[0]" placeholder="选择城市" multiple @change="AreaChoose">
            <el-option-group v-for="group in options2" :key="group.label" :label="group.label">
              <el-option v-for="item in group.options" :key="item.name" :label="item.name" :value="item.id">
              </el-option>
            </el-option-group>
          </el-select>
        </div>
      </div>
      <!-- 行业职位 -->
      <div class="row">
        <div class="head">
          <span class="title" title='行业/职位：'>行业/职位：</span>
        </div>
        <div class="body">
          <!-- 行业1 -->
          <el-select v-model="value3" filterable placeholder="行业类型" @change="changeIndustry1">
            <el-option v-for="(item,index) in options3" :key="index" :label="item.name" :value="item.id">
              <span style="color: #8492a6; font-size: 13px">{{ item.name }}</span>
            </el-option>
          </el-select>
          <!-- 行业2 -->
          <el-select v-model="value4" :disabled="isBan[1]" filterable placeholder="行业" @change="changeIndustry2">
            <el-option v-for="(item,index) in options4" :key="index" :label="item.name" :value="item.id">
              <span style="color: #8492a6; font-size: 13px">{{ item.name }}</span>
            </el-option>
          </el-select>
          <!-- 职位 -->
          <el-select v-model="value5" :disabled="isBan[2]" filterable placeholder="职位" @change="industryChoose">
            <el-option v-for="item in options5" :key="item.name" :label="item.name" :value="item.id">
              <span style="color: #8492a6; font-size: 13px">{{ item.name }}</span>
            </el-option>
          </el-select>
        </div>
      </div>
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.name" clearable placeholder="默认搜索显示招聘列表" style="min-width: 240px;width:400px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="el-icon-search" @click="throttle(searching,this)">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import jobOption from "@/common/jobSearch";
import eventBus from "@/common/eventBus";

export default {
  naem: "job-search",
  data() {
    return {
      filters: jobOption.filters,
      options1: jobOption.options1,
      options2: [
        {
          label: "热门城市",
          options: []
        },
        {
          label: "城市名",
          options: [
            {
              id: 0,
              name: "所有城市",
              label: "所有城市"
            }
          ]
        }
      ],
      options3: jobOption.options3,
      options4: [],
      options5: [],
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      isBan: [false, false, false],
      industryId: jobOption.industryId, //行业id
      regionIds: jobOption.regionIds,
      url: [
        "/position/getLevelAreas",
        "/position/getLevelIndustry",
        "/position/search"
      ] //[各级地区，各级行业，搜索]
    };
  },
  methods: {
    init() {
      let that = this;
      let url = that.HOST + that.url[0];
      that.axios
        .get(url)
        .then(response => {
          jobOption.options1 = [
            {
              id: "",
              name: "所有地区",
              label: "所有地区"
            }
          ];
          Array.prototype.push.apply(jobOption.options1, response.data.message); //合并数组
          that.options1.push.apply(that.options1, response.data.message); //合并数组,此处用Array.prototype.push.apply()数据会传输成功但是页面不会渲染
        })
        .catch(function(response) {
          that.$message.error({
            showClose: true,
            message: "搜索地区数据异常",
            duration: 2000
          });
        });
      url = that.HOST + that.url[1];
      that.axios
        .get(url)
        .then(response => {
          jobOption.options3 = [
            {
              id: "",
              name: "所有行业",
              label: "所有行业"
            }
          ];
          Array.prototype.push.apply(jobOption.options3, response.data.message); //合并数组
          that.options3.push.apply(that.options3, response.data.message); //合并数组
        })
        .catch(function(response) {
          that.$message.error({
            showClose: true,
            message: "搜索行业数据异常",
            duration: 2000
          });
        });
    },
    //二级地区
    changeArea(sel) {
      let that = this;
      // const array = that.options1.filter(a => {
      //   return a.name == sel;
      // });
      // console.log(array);
      if (sel != "") {
        let url = that.HOST + that.url[0];
        let data = { parentId: sel };
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            if (response.data.message != "") {
              that.options2[1].options = [];
              //合并数组
              that.options2[1].options.push.apply(
                that.options2[1].options,
                response.data.message
              );
              that.$set(that.isBan, that.isBan[0], (that.isBan[0] = false));
            } else {
              that.options2 = [
                {
                  label: "热门城市",
                  options: []
                },
                {
                  label: "城市名",
                  options: [
                    {
                      id: 0,
                      name: "所有城市",
                      label: "所有城市"
                    }
                  ]
                }
              ];
              that.$set(that.isBan, that.isBan[0], (that.isBan[0] = true));
              that.value2 = [];
            }
          })
          .catch(response => {
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      }
    },
    //二级行业
    changeIndustry1(sel) {
      let that = this;
      // const array = that.options3.filter(a => {
      //   return a.name == sel;
      // });
      // console.log(array);
      if (sel != "") {
        let url = that.HOST + that.url[1];
        let data = { parentId: sel };
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            if (response.data.message != "") {
              that.options4 = response.data.message;
              //合并数组
              that.$set(that.isBan, that.isBan[1], (that.isBan[1] = false));
            } else {
              that.options4 = [];
              that.$set(that.isBan, that.isBan[1], (that.isBan[1] = true));
              that.$set(that.isBan, that.isBan[2], (that.isBan[2] = true));
              that.value4 = that.value5 = [];
            }
          })
          .catch(response => {
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      }
    },
    //三级行业
    changeIndustry2(sel) {
      let that = this;
      // const array = that.options4.filter(a => {
      //   return a.name == sel;
      // });
      // console.log(array);
      if (sel != "") {
        let url = that.HOST + that.url[1];
        let data = { parentId: sel };
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            if (response.data.message != "") {
              that.options5 = response.data.message; //合并数组
              that.$set(that.isBan, that.isBan[2], (that.isBan[2] = false));
            } else {
              that.options5 = [];
              that.$set(that.isBan, that.isBan[2], (that.isBan[2] = true));
            }
          })
          .catch(response => {
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      }
    },
    //地区选中
    AreaChoose(sels) {
      this.regionIds = sels;
    },
    //行业选中
    industryChoose(sel) {
      this.industryId = sel;
    },
    //搜索
    searching() {
      let that = this;
      jobOption.industryId = that.industryId;
      jobOption.regionIds = that.regionIds;
      jobOption.filters = that.filters;
      console.log(global.currentPath.data)
      if (global.currentPath.data[0] == 8) {
        that.$router.push({
          path: "/recruitment"
        });
      } else {
        console.log("eventBus")
        eventBus.$emit("jobSearch"); //传给jobTable发送请求获取列表
      }
    }
  },
  activated() {
    if (this.options1.length == 1 && this.options3.length == 1) {
      this.init();
    }
  }
};
</script>

<style lang="scss" scoped>
@mixin max {
  width: 100%;
  height: 100%;
}
.active {
  color: #409eff !important;
}
#job-search {
  @include max;
  overflow: hidden;
  padding-bottom: 10px;
}
.current {
  padding: 5px 15px;
  color: #666;
  max-width: 80%;
}
.menus {
  & {
    border: 1px solid #e8e8e8;
    margin: 2px 5px;
    padding: 5px;
  }
  .row {
    & {
      position: relative;
      border-top: 1px dashed #dedede;
      margin: 0 5px;
      // background: #f4f4f4;
    }
    .head {
      & {
        position: absolute;
        left: 11px;
        top: 15px;
        color: rgb(78, 78, 78);
      }
      .title {
        float: left;
        max-width: 100px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        font-weight: 400;
      }
    }
    .body {
      & {
        padding: 6px 80px 7px 85px;
      }
    }
  }
  .row-first {
    border-top: none;
  }
  .el-select {
    margin: 3px 15px 3px 20px;
  }
  .el-form {
    padding: 5px 100px;
  }
  .el-form-item {
    margin: 5px;
  }
}
</style>



